<template>
  <div>
    <div class="form-container">
      <h1></h1>
      <form @submit.prevent="nextStep">
        <div v-if="step == 1">
          <el-tabs v-model="recoverBy">
            <el-tab-pane label="" name="phone">
              <label>
                <span class="title"></span>
                <el-input v-model="form.phone"></el-input>
              </label>
            </el-tab-pane>
            <el-tab-pane label="" name="mail">
              <label>
                <span class="title"></span>
                <el-input v-model="form.mail"></el-input>
              </label>
            </el-tab-pane>
          </el-tabs>
        </div>
        <div v-if="step == 2">
          <label>
            <el-row>
              <el-col :span="18">
                <el-input v-model="form.code" placeholder="" class="input-with-select">
                </el-input>
              </el-col>
              <el-col :span="6">
                <Captcha :sendBy="recoverBy"
                         @send="onCaptchaSend"
                         className="el-button el-button--default"
                         :receiver="form[recoverBy]"/>
              </el-col>
            </el-row>
          </label>
        </div>
        <div v-if="step == 3">
          <label label="">
            <el-input type="text" v-model="form.password" placeholder=""></el-input>
          </label>
        </div>
        <div class="error">
          <div v-for="e in errors" :key="e">{{e}}</div>
        </div>
        <label>
          <button type="submit" class="el-button el-button--primary"></button>
        </label>
      </form>
    </div>
  </div>
</template>

<script>
  import { is }   from '../lib/valee';
  import { send } from '../lib/captcha';
  import Captcha  from '../components/Captcha';

  export default {
    components : { Captcha },
    data () {
      return {
        recoverBy         : 'phone',     // 
        form              : {
          phone    : '',
          mail     : '',
          password : '',
          code     : '',
        },
        sendCodeCountDown : 0,
        errors            : [],
        code              : null,
        codeTimer         : null,
        step              : 1,              //   1 
        user              : null,           //    
      };
    },
    methods    : {
      //  input
      validate () {
        let f         = this.form;
        let e         = this.errors = [];
        let recoverBy = this.recoverBy;

        if (!this.form[ this.recoverBy ]) {
          e.push('');
        }

        if (recoverBy == 'phone' && !is.phone(f.phone))
          e.push('');

        if (recoverBy == 'mail' && !is.mail(f.mail))
          e.push('');

        if (f.password.length < 6)
          e.push('6');

        if (f.code != this.code)
          e.push('');

        if (e.length)
          return false;

        return true;
      }, 
      //     
      onCaptchaSend(code) {
        this.code = code;
      },
      nextStep () {
        let f       = this.form;
        let by      = this.recoverBy;
        this.errors = [];

        switch (this.step) {
          // f[ by ]  form.mailform.phone input 
          case 1:
            if (!f[ by ]) {
              this.errors.push('');
              return;
            }
            // api 
            api('user/first', {
              where : {
                and : {
                  [ by ] : f[ by ],
                },
              },
            }) //   data  user  user 
              .then(r => {
                let user = r.data;
                if (!user) {
                  this.errors.push('');
                } else {
                  console.log(user);
                  this.user = user;
                  this.step++;
                }
              });
            break;
          //   code  
          case 2:
            if (this.code !== f.code) {
              this.errors.push('');
            } else {
              this.step++;
            }
            break;
          //  api 
          case 3:
            let params = {
              id       : this.user.id,   // user.id 
              password : f.password,     //  
              [ by ]   : f[ by ],
            };

            api('user/update', params)
              .then(r => {
                this.$router.push('/login');
              });
            break;
        }
      },
      // sendCode () {
      //   send(this.recoverBy, this.form[ this.recoverBy ], (code) => {
      //     this.code = code;
      //   });
      // },
    },
  };
</script>