<template>
  <div>
    <h1></h1>
    <el-card class="detail" v-if="$route.params.id">
      <div slot="header" class="clearfix">
        <span></span>
        <router-link style="float: right;" to="/my/order"></router-link>
        <!--<el-button style="float: right; padding: 3px 0" type="text"></el-button>-->
      </div>
      <div class="card-content">
        <div class="pair">
          <div></div>
          <div>{{form.id}}</div>
        </div>
        <div class="pair">
          <div></div>
          <div class="cny">{{form.sum}}</div>
        </div>
        <div class="pair">
          <div></div>
          <div>{{lang.payment[form._pay_by] || '-'}}</div>
        </div>
        <div class="pair">
          <div></div>
          <div>{{form._paid ? '': ''}}</div>
        </div>

        <div v-if="!form._paid" style="margin-top: 1em;">
          <div class="title"></div>
          <p>0.2</p>
          <el-button @click="generatePaymentUrl('wechat')"></el-button>
          <el-button @click="generatePaymentUrl('alipay')"></el-button>

          <div v-if="form.$payment">
            <p v-if="form._pay_by === 'wechat'">
              <img class="qrcode" :src="form.$payment.qrcode" alt="">
              <el-button type="primary" @click="read(); find()"></el-button>
            </p>
            <p v-else>
              <a :href="form.$payment.url"></a>
            </p>
          </div>
        </div>
      </div>
    </el-card>
    <el-table
            :data="list"
            stripe
            style="width: 100%">
      <el-table-column
              prop="id"
              label=""
              width="100">
      </el-table-column>
      <el-table-column
              prop="sum"
              label=""
              width="50">
      </el-table-column>
      <el-table-column
              label=""
              width="180">
        <template slot-scope="scope">
          {{scope.row._paid ? '': ''}}
        </template>
      </el-table-column>
      <el-table-column
              fixed="right"
              label=""
              width="100">
        <template slot-scope="scope">
          <el-button type="text" size="small">
            <router-link :to="`/my/order/${scope.row.id}`"></router-link>
          </el-button>
        </template>
      </el-table-column>

    </el-table>
  </div>
</template>

<script>
  import session from '../lib/session';
  import { url } from '../lib/helper';

  export default {
    data () {
      return {
        form   : {},
        list   : [],
        params : {},
        lang   : {
          payment : {
            alipay : '',
            wechat : '',
          },
          status  : {
            created : '',
            paid    : '',
          },
        },
      };
    },
    //   read 
    mounted () {
      this.read();
      //   this.$route.params.id  this.form.id  find()  id: this.form.id 
      this.form.id = this.$route.params.id;
      if (this.form.id)
        this.find();
    },
    methods : {
      find () {
        api('order/find', { id : this.form.id })
          .then(r => this.form = r.data);
      },
      read () {
        api('order/read', { where : { and : { user_id : session.user('id') } } })
          .then(r => this.list = r.data);
      },
      //  
      generatePaymentUrl (type) {
        //  
        this.form._pay_by = type;
        api('order/payment/url', {
          id         : this.form.id,
          pay_by     : type,
          fee        : .2,
          return_url : url('#/my/order'),
        })
          .then(r => {
            this.$set(this.form, '$payment', r.data);
          });
      },
    },
    //   news.params   find()
    watch : {
      '$route' : {
        handler (n, o) {
          let params = n.params;
          if (params.id) {
            this.form.id = params.id;
            this.find();
          }
        },
        deep : true,
      },
    },
  };
</script>

<style scoped>
  .card-content {
    padding: 1em;
  }

  .detail .title {
    margin: .5em 0;
  }

  .qrcode {
    max-width: 200px;
  }
</style>
