<template>
  <el-row>
    <el-col :span="3" class="type">{{propName}}</el-col>
    <el-col :span="21">
      <div 
        @click="select(it)" 
        :key="it" 
        v-for="it in data" 
        :class="'option ' + ((selected && (selected.id == it.id)) ? 'active' : '')"
      >
        {{it.name}}
      </div>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    props   : [ 'data', 'propName', 'initial' ],
    data () {
      return {
        selected : null,      // 
      };
    },
    mounted() {
      //  initial 
      if(this.initial)
        this.selected = this.initial;
    },
    methods : {
      // it     
      select (it) {
        if (this.selected && (this.selected.id == it.id)) {
          this.selected = null;
          this.$emit('clear', it);
          return;
        }
        //   
        this.selected = it;
        this.$emit('change', it);
      },
    },
    //   initial  select 
    watch: {
      initial(n) {
        this.select(n);
      }
    }
  };
</script>

<style scoped>
  .filter .type,
  .filter .option {
    display: inline-block;
    cursor: pointer;
    padding: 1em;
  }

  .filter .option.active {
    background: #409EFF;
    color: #fff;
  }
</style>