import { call as valee } from '../lib/valee';
// admin
export default {
  //   api 
  mounted () {
    this.read();
  },
  data () {
    return {
      ui           : {
        formVisible : false,       // 
      },
      readParam    : {             //    15  
        limit : 10,
        page  : 1,
      },
      list         : [],
      form         : {},            //  vmodel form input
      total        : 0,             // 0
      formOriginal : null,          // form   
      rules        : {},
      errors       : {},
    };
  },
  methods : {
    //   
    flip (page) {
      this.readParam.page = page;
      this.read();
    },

    validate (field) {
      let value      = this.form[ field ];
      let fieldValid = true;
      // 
      // {
      //   lengthBetween : {
      //     params : [ 4, 12 ],
      //       msg    : '412',
      //   }
      //   required : {
      //       msg    : '',
      //   }
      // }
      let rules = this.rules[ field ];

      // 
      for (let key in rules) {
        // lengthBetween
        // {
        //   params : [ 4, 12 ],
        //     msg    : '412',
        // }
        let rule   = rules[ key ];
        let params = rule.params || [];

        // biao valee
        // valee.lengthBetween('whh', 4, 12)
        let valid = valee(key, value, ...params);

        if (typeof valid == 'boolean') {
          this.afterValidate(field, key, valid);
          if (!valid)
            fieldValid = false;
        } else {
          valid.then(r => {
            this.afterValidate(field, key, r);
            // if (!r)
            //   fieldValid = false;
          });
        }
      }

      return fieldValid;
    },

    /**
     * 
     * false
     */
    validateForm () {
      let rules    = this.rules;
      let valid    = true;
      let f        = this.form;
      let isUpdate = !!f.id;

      for (let field in rules) {
        // 
        // whhwhhhh
        // 
        if (isUpdate && f[ field ] === this.formOriginal[ field ])
          continue;

        if (!this.validate(field))
          valid = false;
      }

      return valid;
    },

    /**
     * 
     * @param {string} field
     * @param {bool} valid
     */
    afterValidate (field, rule, valid) {
      // 
      let fieldObj = this.errors[ field ];

      // 
      if (!fieldObj)
        fieldObj = this.$set(this.errors, field, {});

      // valee
      // fieldObj['lengthBetween'] = true;
      this.$set(fieldObj, rule, !valid);
    },
    
    //    row  formOriginal 
    fill (row) {
      console.log(row);  // 
      this.ui.formVisible = true;
      this.formOriginal   = row;
      this.form           = { ...row };
    },
    // 
    remove (id) {
      if (!confirm(''))
        return;

      api(`${this.model}/delete`, { id })
        .then(r => {
          this.read();
        });
    },
    //    apidatatotal
    read () {
      api(`${this.model}/read`, this.readParam)
        .then(r => {
          this.list  = r.data;
          this.total = r.total;
        });
    },
    //   id  
    createOrUpdate () {
      if (!this.validateForm())
      {
        return;
      }

      let f = this.form;
      let action;

      if (f.id)
        action = 'update';
      else
        action = 'create';

      api(`${this.model}/${action}`, f)
        .then(r => {
          this.resetForm();
          this.read();
        });
    },

    resetForm () {
      this.ui.formVisible = false;
      this.form           = {};
    },
    //   
    makeSelect (prop) {
      return it => {
        this.form[ prop ] = it.id;
      };
    },
  },
};