<template>
    <div>
      <div class="form-container">
        <h1></h1>
        <form @submit.prevent="login">
          <label>
            /
            <el-input v-model="form.uiqueName"></el-input>
          </label>
          <label>
            
            <el-input type="text" v-model="form.password"></el-input>
          </label>
          <div class="errors">
            <div v-for="item in errors" :key="item">{{item}}</div>
          </div>
          <label>
            <button type="submit" class="el-button el-button--primary"></button>
            <el-button type="text">
              <router-link to="/recover"></router-link>
            </el-button>
          </label>
        </form>
      </div>
    </div>
</template>

<script>
  import session from '../lib/session';
  export default {
    name: 'Login',
    data () {
      return {
        form: {
          uiqueName: '',
          password: ''
        },
        errors: []
      }
    },
    methods: {
      login () {
        //  validate 
        if(!this.validate())
          return

        let e =this.errors;
        let f = this.form;

        // 
        if(f.uiqueName === 'admin' && f.password === 'yoyoyo') {
          session.login('admin', {nickname: 'Admin', IS_ADMIN: true});
          return
        }

        //  api   phone  mail 
        api('user/first', {
          where: {
            or: [
              ['mail', '=', f.uiqueName],
              ['phone', '=', f.uiqueName]    
            ]
          }
        })
        //     
        .then(r => {
          let user = r.data
          if(!user || user.password !== f.password) {
            e.push('/');
          }else{
            delete user.password;
            session.login(user.id, user);
          }
        })
      },
      // 
      validate () {
        let valid = true;
        let f = this.form;
        let e =this.errors = [];
        if(!f.uiqueName || !f.password) {
          e.push('/');
          valid = false;
        }
        return valid
      }
    },
    components: {
    }
  }
</script>

