//       
//   sync  
// change  product_id , count ,  product_snapshot   

import session     from '../lib/session';
import store       from '../lib/store';
import { obj2Arr } from '../lib/helper';
import api         from '../lib/api';

// localCart     local  
let localCart      = {
  // 1: {               
  //   product: {
  //      ...
  //   }
  //   count: 2
  // },
  // 2: {
  //   count :2
  // }
};
//   localCart  
const callbackPool = [];

const output = {
  //  localCart  callbackPool 
  onChange (fn) {
    if (callbackPool.find(it => it === fn))
      return;

    callbackPool.push(fn);

    // 
    this.callPool();
  },
  /**
   * 
   * @param {number} product_id id
   * @param {number} count 
   * @param {Object} product_snapshot 
   */
  change (product_id, count, product_snapshot, prop, user_id) {
    if (!user_id)
      user_id = session.user('id');

    //       product_snapshot 
    if (!localCart[ product_id ])
      localCart[ product_id ] = { product_id, count : 0, product_snapshot, prop, user_id };

    // 
    let finalCount = localCart[ product_id ].count += count;

    //  0
    if (finalCount <= 0)
      this.remove(product_id);

    // callPool     sync 
    this.callPool();
    this.sync();
  },

  //  callbackPool   localCart   
  callPool () {
    callbackPool.forEach(fn => fn({ ...localCart }));
  },

  //   
  get () {
    return { ...localCart };
  },

  //   callPool   localCart 
  remove (product_id) {
    delete localCart[ product_id ];
    this.callPool();
  },

  // 
  clear () {
    localCart = {};
    this.callPool();
    this.sync();
  },

  //  sync  local
  restore () {
    this.restoreLocal();
    this.callPool();
  },

  //  {}   localCart  local
  restoreLocal () {
    localCart = store.get('cart') || {};
  },

  //  local  
  sync () {
    this.syncCloud();
    this.syncLocal();
  },

  // 
  syncCloud () {
    if (!session.loggedIn())
      return;

    let idList = [];
    this
      //  
      .getCloud()
      .then(r => {
        let data = r.data || [];

        // id: [1, 2, 3]
        data.forEach(row => {
          idList.push(row.id);
        });

        // 
        this.clearCloud(idList);
        // 
        this.overwriteCloud();
      });
  },

  // 
  restoreCloud () {
    this.getCloud({ key_by : 'product_id' })
        .then(r => {
          localCart = r.data;
          this.syncLocal();
          this.callPool();
        });
  },

  // 
  overwriteCloud () {
    api('cart/create_many', {
      data : obj2Arr(localCart),
    });
  },

  //  
  getCloud (param = {}) {
    return api('cart/read', { ...param, where : { and : { user_id : session.user('id') } } });
  },

  // 
  clearCloud (idList) {
    if (!idList.length)
      return;

    return api('cart/delete_many', { in : idList });
  },

  //  localStorage  
  syncLocal () {
    store.set('cart', localCart || {});
  },

  //  
  productCount () {
    localCart = localCart || {}
    return Object.keys(localCart).length;
  },

  // 
  isEmpty () {
    return !this.productCount();
  },
};

//   local
init();

function init () {
  if(session.isAdmin())
    return;

  output.restore();
  if (session.loggedIn())
    output.restoreCloud();
}

export default output;