<template>
  <div>
    <div class="form-container">
      <h1></h1>
      <form @submit.prevent="signup">
        <el-tabs v-model="signupBy">
          <el-tab-pane label="" name="phone">
            <label>
              <span class="title"></span>
              <el-input @blur="uniqueExist" v-model="form.phone"></el-input>
            </label>
          </el-tab-pane>
          <el-tab-pane label="" name="mail">
            <label>
              <span class="title"></span>
              <el-input @blur="uniqueExist" v-model="form.mail"></el-input>
            </label>
          </el-tab-pane>
        </el-tabs>
        <label>
          <fieldset :disabled="!unique">
            <span class="title"></span>
            <el-row>
            <el-col :span="18">
              <el-input v-model="form.code" placeholder="" class="input-with-select">
              </el-input>
            </el-col>
            <el-col :span="6">
              <Captcha :sendBy="signupBy"
                       :receiver="form[signupBy]"
                       @send="onCaptchaSend"
                       className="el-button el-button--default"/>
            </el-col>
          </el-row>
          </fieldset>
        </label>
        <label>
          <span class="title"></span>
          <el-input type="text" v-model="form.password"></el-input>
        </label>
        <div class="error">
          <div v-for="item in errors" :key="item">{{item}}</div>
        </div>
        <label>
          <button class="el-button el-button--primary" type="submit"></button>
          <el-button type="text">
            <router-link to="/login"></router-link>
          </el-button>
        </label>
      </form>
    </div>
  </div>
</template>

<script>
  import { is }  from '../lib/valee';
  import Captcha from '../components/Captcha';

  export default {
    components : { Captcha },
    data () {
      return {
        // phone 
        signupBy: 'phone',
        form: {
          phone: '',
          mail: '',
          password: '',
          code: '',                  // codeinputcode code
        },
        errors            : [],
        code              : null,   // codedatacode code
        unique            : false,
      };
    },
    methods    : {
      // 
      signup () {
        // signup ()
        if (!this.validate())
          return;
        // api
        let f = this.form;
        api('user/create', f)
          .then(r => {
            this.$router.push('/login');
          });
      },

      validate () {
        let f = this.form;
        let e = this.errors = [];
        let signupBy = this.signupBy;
        //  inputfalse   if   errors 
        if (!this.form[ this.signupBy ]) {
          e.push('');
        }
        //  input false  if   errors 
        if (signupBy == 'phone' && !is.phone(f.phone))
          e.push('');

        if (signupBy == 'mail' && !is.mail(f.mail))
          e.push('');

        if (f.password.length < 6)
          e.push('6');

        if (f.code != this.code)
          e.push('');
        //  false
        if (e.length)
          return false;
        //  true
        return true;
      },
      // 
      uniqueExist () {
        let key   = this.signupBy;
        let value = this.form[ this.signupBy ];

        if (!value)
          return;

        api('user/first', {
          where : {
            and : {
              [ key ] : value,
            },
          },
        }).then(r => {
          if (r.data) {
            this.errors.push('');
            this.unique = false;
            return;
          }
          this.unique = true;
        });
      },
      onCaptchaSend (code) {
        this.code = code;
      },
    },
  };
</script>
