<template>
  <div>
    <button @click="send()"
            type="button"
            :class="className"
            :disabled="count !== 0">
      <span v-if="count">{{count}}s</span>
      <span v-else></span>
    </button>
  </div>
</template>

<script>
  import { send } from '../lib/captcha';

  export default {
    props : [
      'sendBy', // phone|mail
      'receiver', // 18399991111|a@b.com
      'className', // 
      'countDown', // 
    ],

    data () {
      return {
        timer : null,
        count : 0, // 
      };
    },

    methods : {
      send () {
        // 0return0 60 
        if (this.count)
          return;
        this.count = this.countDown || 10;
        this.timer = setInterval(() => {
          this.count--;
          if (this.count == 0)
            clearInterval(this.timer);
        }, 1000);
        
        // api captcha/${action} 
        send(this.sendBy, this.receiver)
          .then(r => {
            this.$emit('send', r);       //  
          });
      },
    },
  };
  // let action;
  // if(this.signupBy == 'phone') {
  //   action = 'sms';
  // }else {
  //   action = 'mail'; 
  // }
  // api(`captcha/${action}`, {mail: this.form.mail, phone: this.form.phone})
</script>