<template>
  <div>
    <div class="container">
      <el-row :gutter="10">
        <el-col :span="6">
          <router-link to="/">
            <div class="logo">
              <img src="../img/logo@2x.png" alt="Logo">
            </div>
          </router-link>
        </el-col>
        <el-col class="text-right" :span="12">
          <form @submit.prevent="reload" class="search">
            <el-input v-model="q.keyword" suffix-icon="el-icon-search"></el-input>
          </form>
        </el-col>
        <el-col :span="6">
          <div class="logo">

          </div>
        </el-col>
      </el-row>
    </div>
    <div class="container">
      <div class="filter">
        <div class="table">
          <FilterProp @change="setPropFilter('cat_id', $event)"
                      @clear="removePropFilter('cat_id')"
                      propName=""
                      :initial="filterRestore.cat_id"
                      :data="listCat"/>
          <FilterProp @change="setPropFilter('brand_id', $event)"
                      @clear="removePropFilter('brand_id')"
                      propName=""
                      :initial="filterRestore.brand_id"
                      :data="listBrand"/>
        </div>
        <div class="bar">
          <div class="group">
            <el-button-group>
              <el-button :type="q.sortBy == 'id' ? 'primary' : ''" @click="setSortBy('id')" size="mini">
                <span v-if="q.sortBy == 'id'">
                  <i v-if="q.sortUp == 1" class="el-icon-caret-top"></i><i v-else class="el-icon-caret-bottom"></i>
                </span>
              </el-button>
              <el-button :type="q.sortBy == 'price' ? 'primary' : ''" @click="setSortBy('price')" size="mini">
                <span v-if="q.sortBy == 'price'">
                  <i v-if="q.sortUp == 1" class="el-icon-caret-top"></i><i v-else class="el-icon-caret-bottom"></i>
                </span>
              </el-button>
              <el-button :type="q.sortBy == 'sales' ? 'primary' : ''" @click="setSortBy('sales')" size="mini">
                <span v-if="q.sortBy == 'sales'">
                  <i v-if="q.sortUp == 1" class="el-icon-caret-top"></i><i v-else class="el-icon-caret-bottom"></i>
                </span>
              </el-button>
            </el-button-group>
          </div>
          <div id="price-filter" class="group">
            <el-col :span="12">
              <input @keyup="reload"
                     v-model="q.minPrice"
                     size="mini"
                     placeholder=""
              >
              </input>
            </el-col>
            <el-col :span="12">
              <input @keyup="reload"
                     v-model="q.maxPrice"
                     size="mini"
                     placeholder=""
              >
              </input>
            </el-col>
          </div>
          <div class="group">
            <label>
              <input @click="toggleBool('freeShipping')"
                     :checked="q.freeShipping"
                     type="checkbox">
              
            </label>
            <label>
              <input @click="toggleBool('hasDiscount')"
                     :checked="q.hasDiscount"
                     type="checkbox">
              
            </label>
            <label>
              <input @click="toggleBool('cod')"
                     :checked="q.cod"
                     type="checkbox">
              
            </label>
          </div>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="result">
        <el-row :gutter="5" class="card-list vertical-gutter">
          <el-col :key="it" v-for="it in list" :span="6">
            <ProductCard :data="it"/>
          </el-col>
        </el-row>
        <el-pagination v-if="total != 0"
                       :total="total"
                       class="text-center"
                       layout="prev, pager, next"
                       :page-count="q.limit"
                       :current-page="q.page"  
                       @current-change="flip"
        >              <!-- current-page     v-if="total != 0 -->
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import RegularNav     from '../components/RegularNav';
  import ProductCard    from '../components/ProductCard';
  import { capitalize } from '../lib/helper';
  import FilterProp     from '../components/search/FilterProp';

  export default {
    components : { FilterProp, ProductCard, RegularNav },
    data () {
      return {
        list          : [],    //  
        total         : 0,     //  
        listCat       : [],    //  
        listBrand     : [],    //  
        filterRestore : {
          // brand_id: {           
          //   id: 1,
          //   name: 'lcd'
          // }
        },
        q             : { // this.$route.query  mounted   $route 
          page  : 1,
          limit : 16,
          sortBy: 'id'
        },
      };
    },

    mounted () {
      this.q = { ...this.q, ...this.$route.query, page : parseInt(this.$route.query.page) };  //  q 
      this.read('cat');                                                                       //  cat 
      this.read('brand');                                                                     //  brand 
      this.search();                                                                          
      this.restoreSelectedPropFilter('brand', this.q[ '@brand_id' ], 'brand_id');             // 
      this.restoreSelectedPropFilter('cat', this.q[ '@cat_id' ], 'cat_id');                   // 
    }, 
    methods    : {
      /** 
       *  'http://...?...&@cat_id=1' id = 1cat
       * this.filterRestore <FilterProp/>initial<FilterProp :initial="filterRestore.cat_id"/>
       *
       * @param model         cat  brand
       * @param id             this.q[ '@brand_id' ]  this.q[ '@cat_id' ]
       * @param filterName    brand_id  cat_id
       */
      restoreSelectedPropFilter (model, id, filterName) {
        //   cat  brand  
        if (!id)
          return;
        //  cat  brand 
        api(`${model}/find`, { id })
          .then(r => {
            this.$set(this.filterRestore, filterName, r.data);
          });
      },

      //    prop : cat_id  brand_id  it 
      setPropFilter (prop, it) {
        this.q[ '@' + prop ] = it.id;
        this.reload();
      },

      //      prop : cat_id  brand_id
      removePropFilter (prop) {
        delete this.q[ '@' + prop ];
        this.reload();
      },

      //     
      read (type) {
        api(`${type}/read`)
          .then(r => this[ 'list' + capitalize(type) ] = r.data);
      },

      //      type  1
      toggleBool (type) {
        if (this.q[ type ])
          delete this.q[ type ];
        else
          this.q[ type ] = '1';

        this.reload();
      },

      //   
      setSortBy (type) {
        //  
        if (this.q.sortBy == type) {
          //    else  1 
          if (!this.q.sortUp)
            this.q.sortUp = '1';
          else
            delete this.q.sortUp;
        }

        this.q.sortBy = type;

        this.reload();
      },

      //   $router  watch    reload ()  watch  
      reload () {
        this.$router.push({
          path  : '/search',
          query : { ...this.q },
        });
      },
      forEachPropFilter (fn) {
        let q = this.q;
        for (let key in q) {
          if (!key.startsWith('@'))
            continue;

          fn(q[ key ], key);
        }
      },

      // 
      flip (page) {
        this.q.page = page;
        this.reload();
      },

      //  
      search () {
        let q = this.q;

        let keywordQuery      = `"title" contains "${q.keyword}"`;
        let minPriceQuery     = q.minPrice ? `and "price" >= ${q.minPrice}` : '';
        let maxPriceQuery     = q.maxPrice ? `and "price" <= ${q.maxPrice}` : '';
        let freeShippingQuery = q.freeShipping ? `and "shipping_fee" = 0` : '';
        let hasDiscountQuery  = q.hasDiscount ? `and "discount" > 0` : '';
        let codQuery          = q.cod ? `and "cod" = 1` : '';
        let propFilterQuery   = '';     

        /**
         *        
         * {
         *   '@cat_id': 1
         *   '@brand_id': 2
         *   'sortBy': ['a']
         * }
         * 
         * 'and "cat_id" = 1 and "brand_id" = 2'
         */
        this.forEachPropFilter((value, key) => {
          let prop = key.slice(1);
          propFilterQuery += ` and "${prop}" = "${value}" `;
        });

        //           propFilterQuery 
        let query = `where(
          ${keywordQuery}
          ${minPriceQuery}
          ${maxPriceQuery}
          ${freeShippingQuery}
          ${hasDiscountQuery}
          ${codQuery}
          ${propFilterQuery}
        )`;

        let param = {
          query,
          sort_by : [ (q.sortBy || 'id'), q.sortUp ? 'up' : 'down' ],     // 
          limit   : this.limit,
          page    : q.page,
          limit   : q.limit,
        };

        api('product/read', param)
          .then(r => {
            this.list  = r.data;
            this.total = r.total;
          });
      },
    },

    // RegularNav   $route   $route    $route  this.q 
    watch      : {
      $route : {
        deep : true,
        //  handler 
        handler (news, old) {
          this.q = { ...this.q, ...news.query, page : parseInt(news.query.page) };
          this.search();
        },
      },
    },
  };
</script>

<style scoped>
  .search {
    margin-top: 1em;
    margin-bottom: 1em;
  }

  .filter {
    color: #666;
  }

  .filter > * {
    margin-bottom: .5em;
  }

  .filter .table > * {
    border: 1px solid rgba(0, 0, 0, .1);
    border-bottom-width: 0;
  }

  .filter .table > *:last-child {
    border-bottom-width: 1px;
  }

  .filter .type {
    font-size: 90%;
    opacity: .8;
  }


  .bar > * {
    display: inline-block;
    vertical-align: middle;
  }

  .bar .group {
    margin-right: .5em;
  }

  #price-filter {
    max-width: 14em;
  }

  #price-filter input {
    width: 100%;
  }


  .el-checkbox {
    margin-right: .5em;
  }
</style>